<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process153"></a>

<p><br></p>
<p><span class="rvts17">失事的大篷车（</span><span class="rvts50">The Wrecked Caravan</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:430,Y:40</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：矮人女战士（幸存者）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">向浮木城汇报矮人车队被袭事件</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1114.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">进入死神海岸，在赶往浮木城的大路上，会遇到一些虚空怪。在战斗后，离城门大桥不远处，便会遇到这处被袭的车队。</span>
<br><span class="rvts21">与幸存的矮人女战士谈话，并向浮木城里的魔导师队长朱利安（</span><span class="rvts20">Julian</span><span class="rvts21">）汇报情况。如果此时已经掌握了</span><span class="rvts20"></span><span class="rvts21">源力视野</span><span class="rvts20"></span><span class="rvts21">技能，可以和此处更多的死亡鬼魂谈话，获得跟多的信息。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



